<template>
  <section class="products container">
    <TheTitle 
      :theme="theme"
      :title="categoryName || title"
    />
    
    <!-- Catalogs Wrapper -->
    <div class="products-wrapper" v-if="product_data.length">
      <div
        class="cards"
        v-for="product in product_data"
        :key="product._id"
      >
          <ProductCard 
            :product_data="product"
          />
      </div>
    </div>
    <div class="no-connect" v-else>
      <h1>Loading...</h1>
    </div>
  </section>
</template>

<script>
import TheTitle from '../components/The-title'
import ProductCard from '../components/Product-card'
import axios from 'axios'

export default {
  name: "Products",
  components: {
      TheTitle,
      ProductCard
  },
  data() {
    return {
        theme: 'Main menu / Products',
        title: 'Products',
        category: this.$route.params.category,
        product_data: [],
        categoryName: null
    };
  },
  methods: {
    async getProducts() {
      const dataRes = await axios(`http://old.phoenixglobaltradings.com/products`).then(res => res.data)
      const categoryData = await axios(`http://old.phoenixglobaltradings.com/category/${this.category}`).then(res => res.data)
      this.categoryName = await categoryData.title
      this.product_data = dataRes.filter( data => data.categoryId == this.category)
    }
  }, 
  mounted() {
    this.getProducts()
  },
};
</script>

<style lang="scss">
@mixin media-size($size) {
  @media (max-width: $size) {
    @content;
  }
}

.products{
    min-height: 100vh;
    padding: 220px 0 70px;
    // Media
    @include media-size(1230px) {
        padding: 170px 0 30px;
    }
    @include media-size(790px) {
        padding: 150px 0 30px;
    }
    @include media-size(570px) {
        padding: 150px 15px 30px;
    }
    
    .products-wrapper{
        margin-top: 50px;
        display: flex;
        justify-content: center;
        align-content: flex-start;
        gap: 40px;
        flex-wrap: wrap;
        // Media
        @include media-size(1700px) {
            // gap: 30px;
        }
        
        .cards{
            width: 100%;
            max-width: 380px;
            @include media-size(1700px){
            max-width: 300px;
            }
            @include media-size(1430px){
            max-width: 320px;
            }
            @include media-size(570px){
            max-width: 100%;
            }
        }
    }
    
    .no-connect{
      h1{
        font-size: 80px;
        font-weight: 300;
        color: #999;
        text-align: center;
        width: 100%;
        margin-top: 50px;
        // Media
        @include media-size(1200px){
          font-size: 50px;
        }
        @include media-size(570px){
          font-size: 30px;
        }
      }
    }
}
</style>